import React from "react";
import { practising } from "../data/skills";
import Title from "../components/Title";
import CreateSkill from "./CreateSkill";

function Skills() {
  return (
    <div className="skills">
      <Title title={"Skills"} />
      <div className="practising">
        <p>
          Web Development, System design, Pair programming, Agile (Scrum)
          Methodology, Solution oriented team, Unit testing, Manual testing
        </p>
        {practising.map((skill, index) => {
          return CreateSkill(skill, index);
        })}
      </div>
      <Title title={"What's up"} />
      <div className="learning">
        <p>
          Currently I am exploring the world of test automation with Cypress and
          Python while occasionally helping{" "}
          <a href="https://eacend.com/">Eacend</a> with development of their
          website.
          <br></br>I am also opened for new job opportunities.
        </p>
      </div>
    </div>
  );
}
export default Skills;
