import React from "react";
import Title from "./Title.js";
import Skills from "./Skills";

function About() {
  return (
    <>
      <div className="about">
        <Title title={"Summary"} />
        <div className="motivation">
          <p>
            A <span>quality-oriented</span> person with an{" "}
            <span>eye for detail</span>, who strives to create{" "}
            <span>flawless user experience</span>. Knows the value of{" "}
            <span>clean code</span> and the importance of <span>testing.</span>{" "}
            Enjoys working in a team and using modern methodologies.
          </p>
        </div>
        <Skills />
      </div>
    </>
  );
}

export default About;
